    font-weight: $font-weight-medium;
}

.fw-semibold {
    font-weight: $font-weight-semibold !important;
}

// Flex

.flex-1 {
    flex: 1;
}


// Social

.social-list-item {
    height: 2rem;
    width: 2rem;
    line-height: calc(2rem - 4px);
    display: block;
    border: 2px solid $gray-500;
    border-radius: 50%;
    color: $gray-500;
    text-align: center;
    transition: all 0.4s;

    &:hover {
        color: $gray-600;
        background-color: $gray-200;
    }
}

// Backgroun pattern
.bg-pattern {
    background: url("../images/modal-bg.png") $modal-content-bg;
}

// alert

.alert-dismissible {
    .btn-close {
        font-size: 10px;
        padding: $alert-padding-y * 1.4 $alert-padding-x;
    }
}


// overlay

.bg-overlay {
    position: absolute;
    height: 100%;
    width: 100%;
    right: 0;
    bottom: 0;
    left: 0;
    top: 0;
    background-color: rgba($black, 0.7);
}