//
// Forms.scss
//




[type="tel"],
[type="url"],
[type="email"],
[type="number"] {
 &::placeholder{
   text-align: left /*rtl: right*/;
 }
}

.form-check{
  position: relative;
  text-align: left /*rtl: right*/;
}

.form-check-label, .form-check-input{
  cursor: pointer;
  margin-bottom: 0;
}

.input-group-text{
  margin-bottom: 0px;
}



